$myColor: rgb(2, 83, 97);

h4 {
    color: $myColor;
}
.navBar {
    padding: 5px 0px;
    box-shadow: 0px 0px 2px $myColor;
    background-color: white;

    z-index: 10000;

    .SignOutDiv {
        padding: 5px 0px;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        button {
            background-color: #00838f;
            color: whitesmoke;
        }
    }
    @media (max-width: 760px) {
        .SignOutDiv {
            position: absolute !important;
            top: 30%;
            right: 5%;
            bottom:90px;

            //border: 1px solid #000;
            
            
            button {
                font-size: 14px!important;
                padding: 5px 7px;

    
            }
        }

        

        
    }
}

.sideBar {
    z-index: -1000000;
    overflow: hidden;
    float: left;
    padding: 0;
    box-shadow: 0px 0px 2px #00838f;
    width: 20%;
    height: 90vh;

    .UsersAvailableDiv {
        padding: 6px 10px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        box-shadow: 0px 0px 1px #00838f;

        &:hover,
        &:focus:valid {
            background-color: #00838f23;
        }
    }

    .displayCenter {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.MessagesDiv {
    padding: 0;
    width: 80%;
    border: 1px solid rgba(2, 83, 97, 0.5);
    float: right;
}
