$myColor: rgb(2, 83, 97);
$fullpage: 100vh;
.mainDiv {
    border: 1px solid #000;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: $fullpage;
    width: 100%;

    button {
        background-color: $myColor !important;
        color: whitesmoke;
        outline: none;
        &:hover{
            background-color: rgba(3, 172, 202, 0.815);
            color:whitesmoke
        }
    }
}
h1 {
    color: $myColor;
    margin: 20px 0px;
}
.UserInfo {
    padding: 20px;
    border-radius: 3px;
    box-shadow: 0px 0px 1px $myColor;
    img {
        align-self: stretch;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        box-shadow: 0px 0px 2px $myColor;
        margin: 20px 0px;
    }
    h4 {
        color: rgb(1, 61, 71);
        align-self: stretch;
    }
    h6 {
        align-self: stretch;
        font-size: 15px;
        color: rgb(1, 61, 71);
    }
    .btnDiv {
        margin: 10px 0px;
    }

}

@media (max-width: 375px) {
    .mainDiv {
        .UserInfo {
           width: 250px;
        }
    }
}