.UserLogo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
    h4{
        line-height: 35px;
    }
    img{
        width: 40px;
        height: 40px;
        box-shadow: 0px 0px 1px #006064;
        border-radius: 50%;
        margin: 5px 7px;
        transform: scale(1);
    }
}