$myColor: rgb(2, 83, 97);
.messageList {
    list-style-type: none;
    // border: 1px solid #000;
    padding: 10px 20px;
    box-sizing: border-box;
    margin: 0;
    height: 80vh;
    width: auto;
    display: flex;
    flex-direction: column;

    li {
        margin: 10px 0px;
        
        & > div {
            position: relative;
            width:50%;
            margin: 0px 3px;
        }

        .messageContainer {
            background-color: rgba(2, 83, 97, 0.692);
            width: auto;
            height: auto;
            padding: 4px;
            border-radius: 3px;
            color: whitesmoke;
            border: 1px solid #000;
            display: block;
        }

        .span {
            display: block;
            position: absolute;
            bottom: -16px;
            left: 0%;
            font-size: 13px;
            color:rgba(2, 83, 97, 0.9);
        }
    }
}
.messageDiv {
    height: 90vh;
   
}

.inputDiv {
    clear: both;
    border-top: 1px solid rgba(2, 83, 97, 0.5);
    box-sizing: border-box;
    padding: 10px;
    input {
        width: 90%;
        padding: 5px;
        outline: none;
        border: 1px solid rgba(2, 83, 97, 0.5);
        border-radius: 4px;

        &:focus {
            outline: none;
            color: $myColor;
        }
    }

    .sendBtnDiv {
        width: 10%;
        box-sizing: border-box;
        button {
            background-color: rgb(2, 83, 97);
            color: whitesmoke;
            line-height: 23px;
            margin: 2px;
            font-size: 16px !important;
            font-weight: 500;
        }
    }
}
.displayCenter{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 90vh;
}
.f-left{
    float:left;
}
.f-right{
    float:right;
}
//TabletsScreen....
@media (max-width: 768px) {
    .inputDiv {
        input {
            width: 80%;
            font-size: 15px;
        }

        .sendBtnDiv button {
            font-size: 14px !important;
            height: 30px !important;
            line-height: 14px;
        }
    }
}

//LargeMobileScreen....
@media (max-width: 425px) {
    .inputDiv {
        input {
            width: 75%;
            font-size: 14px;
        }

        .sendBtnDiv button {
            font-size: 14px !important;
            height: 30px !important;
            line-height: 14px;
        }
    }
    .messageContainer {
        font-size: 13px;
    }
}

//SmallMobileScreen....
@media (max-width: 320px) {
    .inputDiv input {
        font-size: 13px;
    }

    .sendBtnDiv button {
        padding: 2px 5px !important;
        font-size: 14px !important;
        height: 30px !important;
        line-height: 14px;
    }
    .messageContainer {
        font-size: 13px;
    }
}
